// app/blogs/[slug]/page.tsx
import { notFound } from 'next/navigation';
import BlogDetailClient from './client';

interface Blog {
  _id: string;
  title: string;
  slug: string;
  content: string;
  excerpt: string;
  image: string;
  altText: string;
  author: string;
  publishedDate: string;
  category: string;
  tags: string[];
  metaTitle: string;
  metaDescription: string;
}

async function getBlog(slug: string): Promise<Blog | null> {
  const res = await fetch(`${process.env.NEXT_PUBLIC_SITE_URL}/api/blogs/${slug}`, {
    cache: 'no-store',
  });

  const data = await res.json();
  return data.success ? data.data : null;
}

async function getRecentBlogs(slug: string): Promise<Blog[]> {
  const res = await fetch(`${process.env.NEXT_PUBLIC_SITE_URL}/api/blogs`, {
    cache: 'no-store',
  });

  const data = await res.json();
  if (!data.success) return [];

  return data.data.filter((b: Blog) => b.slug !== slug).slice(0, 5);
}

export default async function BlogPage({
  params,
}: {
  params: { slug: string };
}) {
  const blog = await getBlog(params.slug);

  if (!blog) {
    notFound();
  }

  const recentBlogs = await getRecentBlogs(params.slug);

  return (
    <BlogDetailClient blog={blog} recentBlogs={recentBlogs} />
  );
}
